import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

import App from './App.vue'
import Menu from './Menu.vue'
import GByH from './GuessByHuman.vue'
import Hamming from './Hamming.vue'

//Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.use(Vuex)

const router = new VueRouter ({
  mode: 'hash',
  routes: [
    {name: 'Menu', path: '/', component: Menu},
    {name: 'GByH', path: '/guessByHuman', component: GByH},
    {name: 'Hamming', path: '/Hamming', component: Hamming}
  ]
})

const store = new Vuex.Store ({
    state: {
        title: 'JYW Learning'
    },
    mutations: {
      setTitle (state, str) {
        state.title = str
      }
    }
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
